.Drawer__Drawer--l2o1Z{
    position: fixed;
    left: 0;
    top: 0;
    bottom: 0;
    width: 80%;
    max-width: 300px;
    padding: 20px 10px;
    box-sizing: border-box;
    background: #fff;
    -webkit-transform: translateX(0px);
            transform: translateX(0px);
    transition: -webkit-transform .22s ease-in;
    transition: transform .22s ease-in;
    transition: transform .22s ease-in, -webkit-transform .22s ease-in;
    z-index: 90;
}

.Drawer__Drawer--l2o1Z.Drawer__close--2WTLc {
    -webkit-transform: translateX(-300px);
            transform: translateX(-300px);
}

.Drawer__Drawer--l2o1Z ul{
    list-style: none;
    margin: 0;
    padding: 0;
}

.Drawer__Drawer--l2o1Z ul li{
    margin-bottom: 15px;
}

.Drawer__Drawer--l2o1Z ul li a {
    color: #363d54;
    font-size: 30px;
    text-decoration: none;
    background-color: #fff;
    position: relative;
    padding: 0 20px 10px 20px;
    transition: opacity .3s;
}

.Drawer__Drawer--l2o1Z ul li a:hover, .Drawer__active--2S50O {
